body {
  margin: 0 auto;
  padding: 0;
}
.is_hidden {
  visibility: hidden;
}
.backdrop {
  visibility: hidden;
  position: fixed;
  display: flex;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgb(106, 82, 190);

  z-index: 2;
}

.dark-mode {
  background-color: #333;
  color: #fff;
}
.logo_dark {
  display: none;
}
.dark_mode {
  display: none;
}
.btn_log-out-desktop {
  display: none;
}
.btn_log-out-mobile {
  display: none;
}
.hamburger_open {
  display: none;
}
.hamburger_close {
  display: none;
}
.header_wrapper {
  width: 100%;
  height: 72px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: '#f6f6f6';
  border: 1.5px solid black;
  border-radius: 0px 0px 8px 8px;
}

.logo {
  position: absolute;
  left: 2%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.home_shopping_list {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 20%;
  cursor: pointer;
}
.shoping-list_text {
  margin-left: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 5;
}

.shoping-list_text:hover::after {
  content: '';
  position: absolute;
  top: -11;
  left: -30;
  background-color: #eac645;
  width: 180px;
  height: 45px;
  border-radius: 45%;
  z-index: -1;
}
.home_text {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 5;
}
.home_text:hover::after {
  content: '';
  position: absolute;
  top: -13;
  left: -10;
  background-color: #eac645;
  width: 70px;
  height: 45px;
  border-radius: 45%;
  z-index: -1;
}
.lock-icon {
  margin-left: 5%;
}
.switcher_button {
  display: flex;
  align-items: center;
  justify-content: center;

  position: absolute;
  right: 15%;
  margin-right: 1%;
}

#themeSwitch {
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
}

.button_login {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 2%;
  background-color: '#f6f6f6';
}
.btn_log-in {
  border: none;
  cursor: pointer;
}
.lock-icon {
  fill: white;
}
.modal_signup {
  width: 579px;
  height: 516px;
  border: 2px solid black;
  border-radius: 18px;
  position: absolute;
  z-index: 5;
  top: 100%;
  background-color: #f6f6f6;
}

.modal_signup-form {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 40px;
  margin-top: 15%;
}

.modal_signup-field-name {
  background-color: #f6f6f6;
  border-radius: 150px 150px 150px 150px;
  width: 499px;
  height: 50px;
  display: flex;
}
.modal_signup-field-email {
  background-color: #f6f6f6;
  border-radius: 150px 150px 150px 150px;
  width: 499px;
  height: 50px;
  display: flex;
}
.modal_signup-field-pass {
  background-color: #f6f6f6;
  border-radius: 150px 150px 150px 150px;
  width: 499px;
  height: 50px;
  display: flex;
}
.modal_signup-name {
  margin-top: 2%;
}
.modal_signup-email {
  margin-top: 2%;
}
.modal_signup-password {
  margin-top: 2%;
}
.modal_input-name {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  width: 370px;
  background-color: #f6f6f6;
  margin-top: 2%;
}
.modal_input-mail {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  width: 370px;
  background-color: #f6f6f6;
  margin-top: 2%;
}
.modal_input-pass {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  width: 325px;
  background-color: #f6f6f6;
  margin-top: 2%;
}
.modal_singup_close {
  position: absolute;
  top: 24;
  right: 24;
  border: none;
  background-color: #f6f6f6;
  cursor: pointer;
}
.modal_btn {
  width: 499px;
  height: 64px;
  background-color: black;
  color: #f6f6f6;
  border-radius: 150px 150px 150px 150px;
  cursor: pointer;
}
.modal_list {
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-right: 50px;
}
.modal_link {
  text-decoration: none;
  color: grey;
}
.modal_link:hover {
  text-decoration-line: underline;
  color: rgb(25, 4, 58);
}

@media screen and (max-width: 768px) and (orientation: portrait) {
  .header_wrapper {
    max-width: 100vw;
    height: 8vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background-color: '#f6f6f6';
    border: 1.5px solid black;
    border-radius: 0px 0px 8px 8px;
  }

  .home_shopping_list {
    display: none;
  }
  .button_login {
    display: none;
  }
  .hamburger_open {
    display: block;
    position: absolute;
    border: none;

    right: 6;
    top: 10;
  }
  #themeSwitch {
    margin-right: 3em;
    margin-top: 0.2em;
  }

  .hamburger_close {
    display: none;
  }
}
